h1 {
	font-size: 25px;
	margin-bottom: 20px;
	text-align: center;
}
.hero {
	height: 100vh;
	&_content {
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
	}
	&_number {
		background-color: yellow;
		width: 200px;
		padding: 20px 0;
		text-align: center;
		margin: 0 auto;
		color: black;
		border-radius: 50px;
		&:hover{
			opacity: 0.5;

		}
	}
	&_container {
		height: 100%;
	}
}

.arrow-8 {
	position: relative;
	width: 100px;
	height: 100px;
	margin: 30px auto 80px auto;
}
.arrow-8:before,
.arrow-8:after {
	content: '';
	position: absolute;
	box-sizing: border-box;
	width: 100%;
	height: 100%;
	border-left: 26px solid #337AB7;
	border-bottom: 26px solid #337AB7;
	animation: arrow-8 3s linear infinite;
}
.arrow-8:after {
	animation: arrow-8 3s linear infinite -1.5s;
}
@keyframes arrow-8 {
	0% {
			opacity: 0;
			transform: translate(0, -53px) rotate(-45deg);
	}
	10%, 90% {
			opacity: 0;
	}
	50% {
			opacity: 1;  
			transform: translate(0, 0) rotate(-45deg);
	}
	100% {
			opacity: 0;
			transform: translate(0, 53px) rotate(-45deg);
	}
}